:root {
  --dark-blue: hsl(233, 26%, 24%);
  --lime-green: hsl(136, 65%, 51%);
  --bright-cyan: hsl(192, 70%, 51%);
  --lime-green-lighter: hsl(136, 65%, 80%);
  --bright-cyan-lighter: hsl(192, 70%, 80%);

  --grayish-blue: hsl(233, 8%, 62%);
  --light-grayish-blue: hsl(220, 16%, 96%);
  --very-light-gray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
}

/* header starts */
.header {
  /* border: 1px solid red; */
  width: 100%;
  height: 50px;
  padding: 1%;
  /* display: flex; */
  /* flex-direction: row; */
  /* justify-content: center; */
  /* align-items: center; */

  display: relative;
}

.header__logo {
  position: absolute;
  left: 5%;
  top: calc(50px * 0.35);
  width: 150px;
  height: 25px;
}

.header__toggle-btn {
  height: 30px;
  aspect-ratio: 1/1;
  top: calc(50px * 0.3);
  right: 8%;

  border: none;
  position: absolute;

  cursor: pointer;
}

.header__toggle-btn--hamburger {
  background: url("/images/icon-hamburger.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.header__toggle-btn--x {
  background: url("/images/icon-close.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.header-nav {
  display: none;
}

.btn--header {
  display: none;
}

.modal {
  position: fixed;
  top: calc(
    50px + 7px
  ); // 50px of the screen height plus 7px of the modal height
  left: 0;
  height: 100vh;
  width: 100vw;
  z-index: 1;

  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  background-color: hsl(233, 26%, 24%, 0.5);

  overflow: hidden;
}

.modal--inactive {
  display: none;
}

.modal-nav {
  width: 90%;
  height: 250px;
  border-radius: 5px;
  position: relative;
  top: 5%;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;

  background-color: var(--white);
  /* background-color: red; */
}

.modal-nav__list {
  /* background-color: red; */
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-evenly;
  align-items: center;
}

.modal-nav__item {
  list-style: none;
}

.modal-nav__link {
  color: var(--dark-blue);
  text-decoration: none;
}

.btn {
  border: none;
  height: 50px;
  width: 175px;
  border-radius: 25px;
  background: var(--bright-cyan);
  background: linear-gradient(
    117deg,
    var(--lime-green) 0%,
    var(--bright-cyan) 100%
  );
  color: var(--white);

  font-size: 0.9rem;
  /* font-weight: bold; */
  /* letter-spacing: 0.5px; */
  font-family: "Public Sans", sans-serif;
  cursor: pointer;
}

.btn:hover {
  background: var(--bright-cyan-lighter);
  background: linear-gradient(
    117deg,
    var(--lime-green-lighter) 0%,
    var(--bright-cyan-hover) 100%
  );
}

.hero {
  width: 100%;
  margin: 0, 0, 5%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-image: url(/images/bg-intro-mobile.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position-y: top -50px;
  overflow: hidden;
}
.hero_img {
  width: 99%;
  margin: 0 1%;
  position: relative;
  top: -140px;
}
.hero_text {
  padding: 5%;
  margin: -40% 0 0;
  text-align: center;
}
.hero_header {
  margin: 0 0 5%;
  font-size: 2rem;
  color: hsl(233, 26%, 24%);
  text-align: center;
}

.hero_copy {
  margin: 0 0 10%;
  color: hsl(233, 8%, 62%);
  font-size: 0.8rem;
  line-height: 1.25rem;
}
.btn {
}

.features {
  width: 80%;
  padding: 15% 10% 10%;
  display: flex;
  flex-direction: column;
  flex-flow: column nowrap;
  justify-content: space-evenly;
  align-items: center;
  background-color: hsl(0, 0%, 98%);
}
.feature__content {
  text-align: center;
}
.features__header {
  margin: 0 0 5%;
  font-size: 2rem;
  font-weight: 400;
  color: hsl(233, 26%, 24%);
}
.features__copy {
  margin: 0 0 10%;
  font-size: 0.8rem;
  font-weight: 200;
  line-height: 1.25rem;
  color: hsl(233, 8%, 62%);
}
.feature__cards {
}
.features-cards {
  min-height: 275px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
}
.features-card__img {
}
.features-card__header {
  font-size: 1.3rem;
  font-weight: 400;
  color: hsl(233, 26%, 24%);
}
.features-card__copy {
  color: hsl(233, 8%, 62%);
  font-size: 0.8rem;
  text-align: center;
  line-height: 1.25rem;
}

.articles {
  padding: 15% 8% 10%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  background-color: white;
}
.articles__header {
  font-size: 2rem;
  font-weight: 400;
  color: hsl(233, 26%, 24%);
}

.articles-card {
  margin: 8% 0;
  border-radius: 5px;
}

.articles-card__img {
  width: 100%;
  border-radius: 5px 5px 0 0;
}

.articles-card__content {
  padding: 10%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  background-color: white;
}

.articles-card__byline {
  margin: 0 0 5%;
  font-size: 0, 7em;
  font-weight: 400;
  color: hsl(233, 8%, 62%);
}

.articles-card__title {
  margin: 0 0 5%;
  font-size: 0.8em;
  color: hsl(233, 26%, 24%);
  font-weight: 400;
}
.articles-card__blurb {
  font-size: 0.75rem;
  color: hsl(233, 8%, 62%);
  font-weight: 400;
}

footer {
  padding: 10% 0;
  display: flex;
  flex-direction: column;
  flex-flow: nowrap;
  justify-content: space-evenly;
  align-items: center;
  background-color: hsl(233, 26%, 24%);
  color: white;
}
.footer__logo-socials {
  width: 50%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
}
.footer__logo {
}
.footer__social {
  margin: 15% 0 5;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-flow: nowrap;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
.footer__social-icon {
}
.footer-nav {
}
.footer-nav__list {
  min-height: 235px;
  display: flex;
  flex-direction: column;
  flex-flow: nowrap;
  justify-content: space-evenly;
  align-items: center;
  list-style: none;
}
.footer-nav__list-item {
}
.footer-nav__list-link {
  font-weight: 300;
  font-size: 0.8rem;
  text-decoration: none;
  color: white;
}
.footer__cat-copyright {
  display: flex;
  flex-direction: column;
  flex-flow: nowrap;
  justify-content: space-evenly;
  align-items: center;
}
.btn .btn-footer {
}
.footer__copyright {
}

.articles {
  padding: 5% 8%;
  flex-flow: row wrap;
  justify-content: flex-start;
}
.articles__header {
  text-align: left;
  width: 100%;
}
.articles-card {
  width: 250px;
  margin: 5% auto 5% 0;
}
.articles-card__img {
  height: 180px;
  width: 100%;
}
.articles-card__byline {
  font-size: 0.65rem;
}
.articles-card__title {
  font-size: 0.85rem;
  line-height: 1.15rem;
}
.articles-card__blurb {
  font-size: 0.67;
  line-height: 1.08rem;
}

@media screen and (min-width: 1200px) {
  .hero_header {
    font-size: 3rem;
  }
}
@media screen and (min-width: 995px) {
  .header__toggle-btn {
    display: none;
  }

  .modal {
    display: none;
  }

  .header {
    padding: 0;
    height: 90px;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    align-items: center;
  }

  .header__logo {
    position: inherit;
    width: 160px;
  }

  .header-nav {
    /* border: 1px solid red; */
    width: clamp(500px, 50%, 550px);
    display: flex;
    justify-content: space-evenly;
    align-items: stretch;

    align-self: stretch;
  }

  .header-nav__list {
    /* border: 1px solid blue; */
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
  }

  .header-nav__list-item {
    /* border: 1px solid blue; */
    list-style: none;
    align-self: stretch;

    /* display: flex;
      flex-flow: column nowrap;
      justify-content: center; */
    position: relative;
  }

  .header-nav__link {
    /* border: 1px solid green; */
    height: 100%;

    text-decoration: none;
    color: var(--grayish-blue);
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
  }

  .header-nav__link:hover::before {
    content: "";
    width: 100%;
    height: 4px;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;

    background: linear-gradient(
      117deg,
      var(--lime-green) 0%,
      var(--bright-cyan) 100%
    );
  }

  .btn {
    scale: 0.9;
  }

  .btn:hover {
    position: relative;
  }

  .btn:hover::after {
    content: "Request Invite";
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 25px;
    width: 100%;
    height: 100%;

    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;

    background: linear-gradient(
      117deg,
      var(--lime-green-lighter),
      var(--bright-cyan-lighter)
    );
  }

  .btn--header {
    display: inline;
  }

  .hero {
    flex-flow: row-reverse nowrap;
    background-color: hsl(0, 0%, 98%);
    background: url(/images/bg-intro-desktop.svg);
    background-size: 90%;
    // background-position-y: top -25vh;
    // background-position-x: right -30vw;
    background-position: top right;
    margin: 0 0 5%;
  }

  .hero_img {
    scale: 1.15;
    position: relative;
    top: -5vh;
    right: -5%;
  }

  .hero_text {
    margin-top: 10vh;
    align-items: stretch;
    margin-left: 5%;
  }
  .hero_header {
    text-align: left;
    font-size: 4.7rem;
    color: black;
    font-weight: lighter;
  }
  .hero_copy {
    width: 80%;
    font-size: 1rem;
    font-weight: normal;
    text-align: left;
  }

  .features {
    width: 100%;
    margin: -15% 0 0;
    padding: 7% 10%;
    flex-flow: row wrap;
    justify-content: flex-start;
    background-color: hsl(220, 16%, 96%);
  }

  .feature__content {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .feature__cards {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
  }

  .features-cards {
    width: 20%;
    margin: 0 1%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: flex-start;
  }

  .features-card__header,
  .features-card__copy {
    text-align: left;
  }
  .features-card__copy {
    font-size: 0.9rem;
    line-height: 1.4rem;
  }
}

@media screen and (min-width: 430px) {
  .hero_text {
    margin: -20% 0 0;
  }

  .features__copy {
    font-size: 1rem;
  }
  .features-card__header {
    font-size: 1.5rem;
  }
  .features-card__copy {
    font-size: 1rem;
  }
  .articles-card__byline {
    font-size: 0.85rem;
  }
  .articles-card__title {
    font-size: 1.1rem;
  }
  .articles-card__blurb {
    font-size: 0.9rem;
  }
  .footer-nav__list-item {
    font-size: 1rem;
  }
}
